import { useState, useContext, useEffect } from 'react'

import Button from '@mui/material/Button'
import TextField from '@mui/material/TextField'
import Dialog from '@mui/material/Dialog'
import DialogActions from '@mui/material/DialogActions'
import DialogContent from '@mui/material/DialogContent'
import DialogContentText from '@mui/material/DialogContentText'
import DialogTitle from '@mui/material/DialogTitle'

import { MySnackbarAlert } from '../alert/MySnackbarAlert'
import { loginAction } from '../../actions/sessionActions'

import { GlobalContext } from '../../App'
import { Typography } from '@mui/material'

export const LoginDialog = ({
  open,
  setOpen,
}: {
  open: boolean,
  setOpen: React.Dispatch<React.SetStateAction<boolean>>,
}) => {
  const [input, setInput] = useState<string>('')
  const { dispatch, session } = useContext(GlobalContext)

  const [openAlert, setOpenAlert] = useState<boolean>(false)

  const isLogin = session.state === 'login'
  const hasError = session.state === 'error'

  const handleClickCancel = () => {
    setOpen(false)
  }

  const handleClickSubmit = async () => {
    loginAction(dispatch, input.toLowerCase())
      .then(() => {
        setOpen(false)
      })
      .catch(() => {
        setOpenAlert(true)
      })
  }

  const handleClickCloseAlert = () => {
    setOpenAlert(false)
  }

  useEffect(() => {
    if (isLogin) setOpen(false)
  }, [isLogin])

  const handleOnInput = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInput(e.target.value)
  }

  return (
    <div>
      <Dialog open={open} onClose={handleClickCancel}>
        <DialogTitle></DialogTitle>
        <DialogContent>
          <DialogContentText>
            
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label=""
            fullWidth
            variant="standard"
            onInput={handleOnInput}
            value={input}
          />
          {hasError
            ?
            <MySnackbarAlert open={openAlert} onClose={handleClickCloseAlert}>
              
            </MySnackbarAlert>
            : null
          }
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClickCancel}></Button>
          <Button onClick={handleClickSubmit} color='primary'></Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
